@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  min-height: 100%;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  display: inline-block;
  margin: 0;
  padding: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover, .button:focus, .button:focus-visible {
  background-color: #5eaa2f;
  outline: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button:disabled, .button--disabled {
  color: rgba(186, 186, 186, 0.6);
  background-color: rgba(94, 170, 47, 0.1);
  pointer-events: none;
}

.title-lines {
  position: relative;
  margin: 0 -20px;
  overflow: hidden;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .title-lines {
    margin: 0 -30px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 46px;
  }
}
@media (min-width: 1440px) {
  .title-lines {
    margin: 0 -102px 0 -110px;
    padding-left: 70px;
  }
}
.title-lines::before, .title-lines::after {
  content: "";
  position: absolute;
  top: 18px;
  display: inline-block;
  width: 100%;
  height: 2px;
  vertical-align: middle;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .title-lines::before, .title-lines::after {
    top: 32px;
  }
}
.title-lines::before {
  display: none;
  margin-left: -100%;
}
@media (min-width: 768px) {
  .title-lines::before {
    display: inline-block;
  }
}
.title-lines::after {
  margin-right: -100%;
}

.title-lines span {
  display: inline-block;
  max-width: 266px;
  padding: 0 20px;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .title-lines span {
    max-width: 100%;
    padding: 0 40px;
  }
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

