.nav {
  position: relative;
  display: grid;
  min-height: 65px;
  grid-template-columns: repeat(2, auto);
}
@media (min-width: 768px) {
  .nav {
    padding: 24px 0 17px;
    gap: 20px;
    grid-template-columns: auto 1fr;
  }
}
@media (min-width: 1440px) {
  .nav {
    padding: 55px 0 20px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nav__toggle {
  position: absolute;
  top: 22px;
  right: 0;
  z-index: 2;
  display: block;
  width: 24px;
  height: 18px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle::before, .nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.nav__toggle::before {
  top: 6px;
}
.nav__toggle::after {
  bottom: 0;
}

.nav--opened .nav__toggle {
  border-top: 2px solid transparent;
}
.nav--opened .nav__toggle::before {
  transform: rotate(-46deg);
}
.nav--opened .nav__toggle::after {
  top: 6px;
  transform: rotate(46deg);
}

.no-js .nav__toggle {
  display: none;
}

.no-js .nav {
  position: static;
}

.nav__list {
  position: absolute;
  top: 100%;
  right: -20px;
  left: -20px;
  z-index: 10;
  display: none;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: 4px 4px 0 0;
    background-color: transparent;
    gap: 34px;
  }
}
@media (min-width: 1440px) {
  .nav__list {
    margin: 0;
    padding-top: 10px;
  }
}

.nav--opened .nav__list {
  display: flex;
}

.nav__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    border: none;
  }
}

.nav__link {
  position: relative;
  display: block;
  padding: 21px 20px 24px;
  padding-top: 21px;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .nav__link {
    padding: 6px 0;
    font-size: 20px;
    line-height: 30px;
  }
}
.nav__link:hover, .nav__link:focus, .nav__link:focus-visible {
  opacity: 0.6;
}
.nav__link:active {
  opacity: 0.3;
}

.nav__link--current {
  pointer-events: none;
}

@media (min-width: 768px) {
  .nav__link--current::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .nav__link--current::before {
    bottom: -2px;
  }
}

@media (min-width: 1440px) {
  .main-header--home .nav__link--current::before {
    background-color: #ffffff;
  }
}

@media (min-width: 1440px) {
  .main-header--home .nav__link {
    color: #ffffff;
  }
}

